
import './styles.css'

import Breadcumb from "../Breadcumb"
import SettingsLogo from "../../icons/cog.svg";

import useSound from "use-sound"
import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";
import { useState, useEffect } from "react"

import ReactCountryFlag from "react-country-flag"

import { useTranslation, Trans } from 'react-i18next';

const langs = {
    en: { nativeName: 'English', code:'GB' },
    es: { nativeName: 'Espaol (Traducido por: Giquo)', code:'ES' },
    cn: { nativeName: ' (Sanya V litvyak)', code:'CN' },
    jp: { nativeName: ' (Sorasenkai)', code:'JP' },
    ru: { nativeName: ' (: RussianShark)', code:'RU' },
    pt: { nativeName: 'Portugus (traduzido por: Darkskryll)', code:'BR' },
    it: { nativeName: 'Italiano (Tradotto da: Giquo)', code:'IT' },
};

const SettingsPage = () => {
    const { t, i18n } = useTranslation();

    const [ pageFont, setPageFont ] = useState("7");
    const [ pageTheme, setPageTheme ] = useState("7");
    const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
    const [ playSelect ] = useSound( SelectSfx, {volume:1} );

    useEffect(() => {
        let fontStored = JSON.parse(localStorage.getItem('font'));
        let themeStored = JSON.parse(localStorage.getItem('theme'));
        
        if ( fontStored ) {
            setPageFont( fontStored );
        }

        if ( themeStored ) {
            setPageTheme( themeStored );
        }
    });


    const selectFontType = ( fontName ) => {
        let body = document.getElementsByTagName('body')[0];

        body.style.fontFamily = selectFontTypeClass( fontName );
        
        localStorage.setItem('font', JSON.stringify(fontName));
        setPageFont( fontName );
        playSelect();
    }

    const selectTheme = ( theme ) => {
        console.log( theme );
        let body = document.getElementsByTagName('body')[0];

        if ( theme === '7' ) {
            document.body.classList.add('ac7-theme');
            document.body.classList.remove('acz-theme');
            document.body.classList.remove('ac5-theme');
            
        } else if ( theme === 'Z' ) {
            document.body.classList.add('acz-theme');
            document.body.classList.remove('ac7-theme');
            document.body.classList.remove('ac5-theme');

        } else if ( theme === '5' ) {
            document.body.classList.add('ac5-theme');
            document.body.classList.remove('ac7-theme');
            document.body.classList.remove('acz-theme');
        }
        
        localStorage.setItem('theme', JSON.stringify(theme));
        setPageTheme( theme );
        playSelect();
    }

    return (
        <div className="tw-flex tw-flex-row tw-h-screen tw-w-screen tw-justify-center">

            <div className="tw-flex tw-flex-col lg:tw-w-3/5 tw-p-4">
                <Breadcumb secondLevel={t('home.settings')} iconSrc={ SettingsLogo }></Breadcumb>

                <p className="tw-text-2xl tw-text-left">
                {t('settings.lang')}<br />
                </p>
                <i>{t('settings.langConsequence')}</i>
                <ul>
                    {Object.keys(langs).map((lng) => (
                        <li className={`tw-text-2xl ${i18n.resolvedLanguage === lng ? 'active-list-item' : 'inactive-list-item'}`} key={lng} onClick={() => i18n.changeLanguage(lng)}>
                            <ReactCountryFlag countryCode={ langs[lng].code } style={{ fontSize: "2em" }} aria-label={ langs[lng].nativeName }/> - {langs[lng].nativeName}
                        </li>
                    ))}
                </ul>
                


                
                <p className="tw-text-2xl tw-text-left">
                    <br />
                    {t('settings.platformFont')}<br />
                </p>
                <ul>
                    
                    <li className={`tw-text-2xl ac7-font-item ${pageFont === '7' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('7') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat 7 - Skies Unknown
                    </li>

                    <li className={`tw-text-2xl ac6-font-item ${pageFont === '6' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('6') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat 6 - Fires of Liberation (OCR-A Regular)
                    </li>
                    
                    <li className={`tw-text-2xl ac4-font-item ${pageFont === '4' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('4') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat 4 - Shattered Skies
                    </li>

                    <li className={`tw-text-2xl ac5-font-item ${pageFont === '5' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('5') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat 5 (Neue Pixel Sans by <a href="https://www.dafont.com/es/daymarius.d7345">Daymarius</a>)
                    </li>

                    <li className={`tw-text-2xl acz-font-item ${pageFont === '0' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('0') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat Zero (Envy Code R)
                    </li>

                    <li className={`tw-text-2xl stencil-font-item ${pageFont === 'chakra' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('chakra') } onMouseEnter={ playNavigate }>
                        &gt; Stencil (Chakra Petch)
                    </li>

                    <li className={`tw-text-2xl play-font-item ${pageFont === 'play' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('play') } onMouseEnter={ playNavigate }>
                        &gt; Play
                    </li>

                    <li className={`tw-text-2xl xolonium-font-item ${pageFont === 'xolonium' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('xolonium') } onMouseEnter={ playNavigate }>
                        &gt; Xolonium
                    </li>

                    <li className={`tw-text-2xl yusei-font-item ${pageFont === 'yusei' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('yusei') } onMouseEnter={ playNavigate }>
                        &gt; Yusei Magic
                    </li>
                </ul>


                <p className="tw-text-2xl tw-text-left">
                <br />
                    {t('settings.theme')}<br />
                </p>
                <ul>
                    
                    <li className={`tw-text-2xl ${pageTheme === '7' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectTheme('7') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat 7 - Skies Unknown
                    </li>

                    <li className={`tw-text-2xl ${pageTheme === 'Z' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectTheme('Z') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat Zero - The Belkan War
                    </li>

                    <li className={`tw-text-2xl ${pageTheme === '5' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectTheme('5') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat 5 - The Unsung War
                    </li>
                </ul>

            </div>
        </div>
    )
}

const selectFontTypeClass = ( fontName ) => {
    let fontFamily = 'sans-serif';

    switch ( fontName ) {
        case '7':
            fontFamily = "'AC7-Regular', sans-serif";
            break;

        case '6':
            fontFamily = "'OCR-A', monospace";
            break;

        case '4':
            fontFamily = "'ACES04', sans-serif";
            break;

        case '5':
            fontFamily = "'AC5-Neue', monospace";
            break;

        case '0':
            fontFamily = "'ACZ-Envy', sans-serif";
            break;

        case 'chakra':
            fontFamily = "'Chakra-Petch', sans-serif";
            break;

        case 'play':
            fontFamily = "'Play', sans-serif";
            break;

        case 'xolonium':
            fontFamily = "'Xolonium', sans-serif";
            break;

        case 'yusei':
            fontFamily = "'Yusei', monospace";
            break;

        default:
            break;
    }

    return fontFamily;
}

export { selectFontTypeClass }
export default SettingsPage;
