
import packageJson from '../../../package.json';

import './styles.css'

import { useState, useEffect } from 'react';

import { Link } from "react-router-dom"
import useSound from "use-sound"
import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";

import KeyActionSuggestion from './KeyActionSuggestion';

import Konami from 'react-konami-code';
import { useTranslation, Trans } from 'react-i18next';

const HomePage = () => {
    const { t } = useTranslation();
    
    const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
    const [ playSelect ] = useSound( SelectSfx, {volume:1} );
    const [ helpMessage ] = useState('Fullscreen mode will give you a better experience');
    
    useEffect(() => {
        document.title = `Ace Combat 7 PSM Multiplayer Pilots Database`;
    });

    const emulateF8Toggle = () => {
        window.dispatchEvent(new KeyboardEvent('keydown', {
            'key': 'F8'
        }));
    }

    return (
        <div className="tw-h-screen tw-w-screen tw-flex tw-flex-row tw-justify-center">
            <div className="main-home-page-container tw-flex tw-flex-col tw-w-9/12 tw-p-4">
                <h1 className="tw-text-3xl lg:tw-text-5xl tw-pt-20">{t('home.title')}</h1>
                <small className='tw-pb-20'>{packageJson.version}</small>
                <ul>
                    <li className="tw-text-2xl inactive-list-item">
                        <Link to="/pilots" onMouseEnter={ playNavigate } onClick={ playSelect }>{t('home.pilots')}</Link>
                    </li>

                    <li className="tw-text-2xl inactive-list-item">
                        <Link to="/rules" onMouseEnter={ playNavigate } onClick={ playSelect }>{t('home.rules')}</Link>
                    </li>

                    <li className="tw-text-2xl inactive-list-item">
                        <Link to="/settings" onMouseEnter={ playNavigate } onClick={ playSelect }>{t('home.settings')}</Link>
                    </li>
                    
                    <li className="tw-text-2xl inactive-list-item">
                        <Link to="/about" onMouseEnter={ playNavigate } onClick={ playSelect }>{t('home.about')}</Link>
                    </li>
                    <Konami action={playSelect}>
                        <li className="tw-text-2xl inactive-list-item">
                            <Link to="/average-test" onMouseEnter={ playNavigate } onClick={ playSelect }>Average Test</Link>
                        </li>
                    </Konami>
                </ul>
                <div className="tw-flex tw-my-auto"></div>
                <ul>
                    <li className="tw-text-2xl inactive-list-item">
                        <Link to="/changelog" onMouseEnter={ playNavigate } onClick={ playSelect }>{t('home.changelog')}</Link>
                    </li>
                </ul>
                <div className="tw-flex tw-flex-row tw-justify-end tw-py-16">
                    <p className="tw-text-xl tw-text-right wppr">
                    {t('home.fullScreenInfo')}
                    </p>
                </div>
                <div className="sm:tw-flex tw-flex-row tw-hidden">
                    <KeyActionSuggestion keyName="F11" description={t('home.fullscreen')} />
                </div>
            </div>
        </div>
    )
}

export default HomePage
