
import { useTranslation, Trans } from 'react-i18next';

const InformationButton = ({ onMouseEnterAction, onClickAction }) => {
    const { t, i18n } = useTranslation();

    return (
        <div className="tw-flex tw-flex-row">
            <p className="inactive-player-list-item tw-text-base" onMouseEnter={ onMouseEnterAction } onClick={ onClickAction }>[{t('pilots.infoLbl')}]</p>
        </div>
    )
}

export default InformationButton
