
import useSound from "use-sound"
import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";

import { FaSteam, FaPlaystation, FaXbox } from "react-icons/fa";

import { useTranslation, Trans } from 'react-i18next';

const PlayersFilter = ({ selectFilter, filter }) => {
    const { t, i18n } = useTranslation();

    const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
    const [ playSelect ] = useSound( SelectSfx, {volume:1} );

    const select = ( e ) => {
        selectFilter( e );
        playSelect();
    }

    return (
        <div className="tw-flex tw-flex-row tw-justify-around tw-py-2">
            <div onClick={ () => select('all') } className={`player-list-item-name ${filter === "all" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                {t('common.all')}
            </div>
            <div onClick={ () => select('PC') } className={`tw-flex tw-flex-row tw-items-center player-list-item-name ${filter === "PC" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                <FaSteam />
                <span className="tw-pl-2">STEAM</span>
            </div>
            <div onClick={ () => select('PSN') } className={`tw-flex tw-flex-row tw-items-center player-list-item-name ${filter === "PSN" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                <FaPlaystation />
                <span className="tw-pl-2">PSN</span>
            </div>
            <div onClick={ () => select('XBOX') } className={`tw-flex tw-flex-row tw-items-center player-list-item-name ${filter === "XBOX" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                <FaXbox />
                <span className="tw-pl-2">XBOX</span>
            </div>
        </div>
    )
}

export default PlayersFilter
