
import React from "react";
import useSound from "use-sound";

import NavigateSfx from "../../sfx/navigate.mp3";

import { FaBackward, FaForward, FaPlay, FaPause } from "react-icons/fa";

const AudioControls = ({ isPlaying, onPlayPauseClick, onPrevClick, onNextClick }) => {

	const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );

	return (
		<div className="audio-controls tw-flex tw-flex-row tw-py-2 tw-justify-center">
			<div className="tw-px-2">
				<span className="music-player-icon tw-text-2xl" onClick={ onPrevClick }>
					<FaBackward />
				</span>
			</div>
			<div className="tw-px-2">
			{isPlaying ? (
				<span className="music-player-icon tw-text-2xl" onClick={() => onPlayPauseClick(false)} onMouseEnter={ playNavigate }>
					<FaPause />
				</span>
			) : (
				<span className="music-player-icon tw-text-2xl" onClick={() => onPlayPauseClick(true)} onMouseEnter={ playNavigate }>
					<FaPlay />
				</span>
    		)}
			</div>
			<div className="tw-px-2">
				<span className="music-player-icon tw-text-2xl" onClick={ onNextClick } onMouseEnter={ playNavigate }>
					<FaForward />
				</span>
			</div>
		</div>
	);

};

export default AudioControls;
