
import "./styles.css"

import { useState, useEffect, useRef } from "react";
import useSound from "use-sound";

import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";

import { FaPlayCircle, FaChevronDown, FaSpinner } from "react-icons/fa";

import AudioControls from './AudioControls';

import FullPlaylist from './Playlist';

const Music = ({ musicState }) => {

	const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
	const [ playSelect ] = useSound( SelectSfx, {volume:1} );

	const [ showMiniPlayer, setShowMiniPlayer] = useState(true);

	const [ playlist, setPlaylist ] = useState([
		{
			"src":"https://vgmsite.com/soundtracks/ace-combat-7-skies-unknown-original-soundtrack/zelrqndtxj/1-01%20Skies%20Unknown.mp3",
			"title":"Skies Unknown",
			"album":"Ace Combat 7: Skies Unknown",
			"thumbnail":"ac7.webp"
		}]);

	const [ trackIndex, setTrackIndex ] = useState(0);
	const [ trackProgress, setTrackProgress ] = useState(0);
	const [ isPlaying, setIsPlaying ] = useState(false);
	const [ isLoadingTrack, setIsLoadingTrack ] = useState(false);

	// Destructure for conciseness
	const { title, album, thumbnail, src } = playlist[trackIndex];

	// Refs
	const audioRef = useRef(new Audio(src));
	const intervalRef = useRef();
	const isReady = useRef(false);

	// Destructure for conciseness
	const { duration } = audioRef.current;

	const shuffle = (array) => {
		let currentIndex = array.length, randomIndex;
		// While there remain elements to shuffle...
		while (currentIndex !== 0) {
			// Pick a remaining element...
			randomIndex = Math.floor(Math.random() * currentIndex);
			currentIndex--;

			// And swap it with the current element.
			[array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
		}
		return array;
	}


	const toPrevTrack = () => {
		if (trackIndex - 1 < 0) {
			setTrackIndex(playlist.length - 1);
		} else {
			setTrackIndex(trackIndex - 1);
		}
	}

	const toNextTrack = () => {
		if (trackIndex < playlist.length - 1) {
			setTrackIndex(trackIndex + 1);
		} else {
			setTrackIndex(0);
		}
	}

	/*const togglePlay = (event) => {
		if (event.keyCode === 118) {
			event.preventDefault();
			toPrevTrack();
		} else if (event.keyCode === 119) {
			event.preventDefault();
			console.log( isPlaying );
			isPlaying ? setIsPlaying( false ) : setIsPlaying( true );
		} else if (event.keyCode === 120) {
			event.preventDefault();
			toNextTrack();
		}
	}*/

	const fetchplaylist = async () => {
        let url = process.env.NODE_ENV === 'development' ? "http://localhost:5000/playlist" : process.env.PUBLIC_URL + "/database/playlist.json";
        const res = await fetch( url );
        const data = await res.json();
        return data.playlist ? data.playlist : data;
	}

	const [ showFullCompPlayer, setShowFullCompPlayer] = useState(false);

	useEffect(() => {
		const getMusic = () => {
            let trackListFetched = FullPlaylist;
			trackListFetched = shuffle( trackListFetched );
            setPlaylist ( trackListFetched );
			
			setShowFullCompPlayer( true );
			setTrackIndex(1);
			setTimeout( () => { 
				setIsPlaying(true);
			}, 1000);
        }
		
		if ( musicState ) {
			getMusic();
		}
		
		//document.addEventListener("keydown", togglePlay);
		//audio.addEventListener("ended", playNextSong);
		
	}, [musicState]);

	useEffect(() => {
		if (isPlaying) {
			try {
				setIsLoadingTrack(true);
				
				audioRef.current.play()
				.then(()=>{
					setIsLoadingTrack(false);
				})
				.catch(()=>{
					setIsLoadingTrack(false);
				});

				startTimer();
				
			} catch (error) {
				console.log( error );
			}
		} else {
			audioRef.current.pause();
		}
	}, [isPlaying]);

	useEffect(() => {
		// Pause and clean up on unmount
		return () => {
			audioRef.current.pause();
			clearInterval(intervalRef.current);
		}
	}, []);

	// Handle setup when changing tracks
	useEffect(() => {
		audioRef.current.pause();
	
		audioRef.current = new Audio( src );

		audioRef.current.load();
		//audioRef.current.src = src
		setTrackProgress(audioRef.current.currentTime);
	
		if (isReady.current) {
			try {
				setIsLoadingTrack(true);
			
				audioRef.current.play()
				.then(()=>{
					setIsLoadingTrack(false);
				})
				.catch(()=>{
					setIsLoadingTrack(false);
				});

				setIsPlaying(true);
				startTimer();
				
			} catch (error) {
				console.log( error );
			}
		} else {
			// Set the isReady ref as true for the next pass
			isReady.current = true;
		}
	}, [trackIndex]);

	const currentPercentage = duration ? `${(trackProgress / duration) * 100}%` : '0%';
	const trackStyling = `-webkit-gradient(linear, 0% 0%, 100% 0%, color-stop(${currentPercentage}, #fff), color-stop(${currentPercentage}, #777))`;

	const startTimer = () => {
		// Clear any timers already running
		clearInterval(intervalRef.current);

		intervalRef.current = setInterval(() => {
			if (audioRef.current.ended) {
				toNextTrack();
			} else {
				setTrackProgress(audioRef.current.currentTime);
			}
		}, [1000]);
	}

	const onScrub = (value) => {
		// Clear any timers already running
		clearInterval(intervalRef.current);
		audioRef.current.currentTime = value;
		setTrackProgress(audioRef.current.currentTime);
	}
	
	const onScrubEnd = () => {
		// If not already playing, start
		if (!isPlaying) {
			setIsPlaying(true);
		}
		startTimer();
	}

	const toggleMiniPlayer = () => {
		let newVal = showMiniPlayer;
        setShowMiniPlayer(!newVal);
		playSelect();
	}

		/////////////////////////////////////////////////////////////////////////////////////////////////////

	return (
		<div className={`music-player ${showFullCompPlayer ? '' : 'tw-hidden'} ${!showMiniPlayer ? 'music-player-fab' : ''}`}>
			<span className={`music-player-icon tw-text-2xl ${!showMiniPlayer ? '' : 'tw-hidden'}`} onClick={() => toggleMiniPlayer()} onMouseEnter={ playNavigate }>
				<FaPlayCircle />
			</span>

			<div className={`music-player-track tw-flex sm:tw-w-full tw-flex-col tw-items-center tw-px-3 tw-py-1 ${showMiniPlayer ? '' : 'tw-hidden'}`}>

				<div className={`tw-flex tw-flex-row tw-items-center`}>

					<div className={`tw-flex sm:tw-flex-row tw-flex-col tw-items-center`}>

						<div className={`tw-flex tw-flex-row tw-items-center tw-w-full`}>
							<div className="tw-pr-2">
								<img 
								src={process.env.PUBLIC_URL + `/albumart/${thumbnail}`} 
								alt={`track artwork for ${title} from ${album}`} 
								height="56" 
								width="56"/>
							</div>
							<FaSpinner className={`${isLoadingTrack ? 'spinner' : 'tw-hidden'}`}/>
							<div className="tw-flex tw-flex-col tw-px-2">
								<h3>{ title }</h3>
								<h5>{ album }</h5>
							</div>
						</div>
						
						<AudioControls isPlaying={isPlaying} onPrevClick={toPrevTrack} onNextClick={toNextTrack} onPlayPauseClick={setIsPlaying} />

					</div>
					
				</div>

				<div className={`tw-flex tw-flex-row tw-items-center tw-w-full tw-pt-2`}>
					<input
						type="range"
						value={trackProgress}
						step="1"
						min="0"
						max={duration ? duration : `${duration}`}
						className="progress"
						onChange={(e) => onScrub(e.target.value)}
						onMouseUp={onScrubEnd}
						onKeyUp={onScrubEnd}
						style={{ background: trackStyling }}
					/>

					<span className="tw-pl-2 music-player-icon tw-text-2xl" onClick={ toggleMiniPlayer } onMouseEnter={ playNavigate }>
						<FaChevronDown />
					</span>
				</div>

			</div>
		</div>
	)
}

export default Music
