
import './styles.css';

import { useState, useEffect } from 'react';
import PlayerListItem from './PlayerListItem';
import PlayersFilter from './PlayersFilter';

import useSound from "use-sound"
import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";

import ReactCountryFlag from "react-country-flag"
import { FaArrowUp, FaArrowDown } from "react-icons/fa";

import { useTranslation, Trans } from 'react-i18next';

import { determineRankByAverage } from './rankingCoefficientCalculation';

const PlayerList = ({ vctPlayers, onSelectPilot, showForMobile, showHiddenPlayers }) => {
    const { t, i18n } = useTranslation();

    const [ scrollThumb, setScrollThumb ] = useState("tw-scrollbar-thumb-blue-700");
    const [ scrollTrack, setScrollTrack ] = useState("tw-scrollbar-track-blue-300");

    const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
    const [ playSelect ] = useSound( SelectSfx, {volume:1} );

    const [ sorting, setSorting ] = useState("Rank");
    const [ sortingDirection, setSortingDirection ] = useState("ascending");

    const [platformFilter, setPlatformFilter] = useState('all');
    const [vctCountries, setVctCountries] = useState([]);
    const [countryFilter, setCountryFilter] = useState('all');

    const [countLegacyPilots, setCountLegacyPilots] = useState(false);
    const [filteredPlayers, setFilteredPlayers] = useState(vctPlayers);

    const [ssRankPilots, setSsRankPilots] = useState([]);
    const [s1RankPilots, setS1RankPilots] = useState([]);
    const [s2RankPilots, setS2RankPilots] = useState([]);
    const [s3RankPilots, setS3RankPilots] = useState([]);
    const [a1RankPilots, setA1RankPilots] = useState([]);
    const [a2RankPilots, setA2RankPilots] = useState([]);
    const [a3RankPilots, setA3RankPilots] = useState([]);
    const [b1RankPilots, setB1RankPilots] = useState([]);
    const [b2RankPilots, setB2RankPilots] = useState([]);
    const [b3RankPilots, setB3RankPilots] = useState([]);
    const [cRankPilots, setCRankPilots] = useState([]);

    useEffect(()=>{
        let theme = JSON.parse(localStorage.getItem('theme'));

        if ( !theme || theme === '7' ) {
            setScrollThumb( "tw-scrollbar-thumb-blue-700" );
            setScrollTrack( "tw-scrollbar-track-blue-300" );
            
        } else if ( theme === 'Z' ) {
            setScrollThumb( "tw-scrollbar-thumb-red-700" );
            setScrollTrack( "tw-scrollbar-track-red-300" );

        } else if ( theme === '5' ) {
            setScrollThumb( "tw-scrollbar-thumb-green-700" );
            setScrollTrack( "tw-scrollbar-track-green-300" );
        }

        countCountries( platformFilter, countLegacyPilots );

        if ( vctPlayers ) {
            if ( vctPlayers.length > 0 ) {
                filterPlayers( platformFilter, countryFilter, sorting, sortingDirection, countLegacyPilots );
            }
        }
    },[vctPlayers]);

    const countCountries = ( plat, countLegacy ) => {
        let metaVct = [], newPlayers=[], alphaPlayers=[];

        if ( countLegacy ) {
            alphaPlayers = vctPlayers.slice();
        } else {
            for (const player of vctPlayers) {
                if ( player.active ) {
                    alphaPlayers.push( player ); 
                }
            }
        }

        if ( plat === 'all' ) {
            newPlayers = alphaPlayers.slice()
        } else {
            for (const player of alphaPlayers) {
                if ( player.platforms.indexOf( plat ) > -1 ) {
                    newPlayers.push( player ); 
                }
            }
        }

        newPlayers.forEach(player => {
            let found = false;

            for (let i = 0; i < metaVct.length; i++) {
                const country = metaVct[i];
                
                if ( country.code === player.country.code ) {
                    country.cant = country.cant + 1;
                    found = true;
                    break;
                }
            }

            if ( !found ) {
                metaVct.push({ 
                    code:player.country.code, cant:1 
                });
            }
        });

        metaVct.sort(function (a, b) {
            return (a.cant > b.cant) ? -1 : (a.cant < b.cant) ? 1 : 0;
        });

        setVctCountries( metaVct );
    }

    const selectSortingMethod = ( sort ) => {
        setSorting( sort );
        playSelect();
        filterPlayers( platformFilter, countryFilter, sort, sortingDirection, countLegacyPilots );
    }

    const selectSortingDirection = ( dir ) => {
        setSortingDirection( dir );
        playSelect();
        filterPlayers( platformFilter, countryFilter, sorting, dir, countLegacyPilots );
    }

    const selectPlatformFilter = ( filter ) => {
        setPlatformFilter( filter );
        countCountries( filter, countLegacyPilots );
        filterPlayers( filter, countryFilter, sorting, sortingDirection, countLegacyPilots );
    }

    const selectCountryFilter = ( pais ) => {
        setCountryFilter( pais );
        playSelect();
        filterPlayers( platformFilter, pais, sorting, sortingDirection, countLegacyPilots );
    }

    const setLegacyPilots = ( lg ) => {
        setCountLegacyPilots( lg ) 
        playSelect();
        countCountries( platformFilter, lg );
        filterPlayers( platformFilter, countryFilter, sorting, sortingDirection, lg );
    }

    const groupPlayersByRank = ( lastVct ) => {
        let ssList=[], s1List=[], s2List=[], s3List=[], a1List=[], a2List=[], a3List=[], b1List=[], b2List=[], b3List=[], cList=[];

            lastVct.forEach((pilot, idx) => {                
                switch ( determineRankByAverage( pilot.rankAverage ) ) {
                    case 'SS':
                        ssList.push( pilot );
                        break;
                    case 'S1':
                        s1List.push( pilot );
                        break;
                    case 'S2':
                        s2List.push( pilot );
                        break;
                    case 'S3':
                        s3List.push( pilot );
                        break;
                    case 'A1':
                        a1List.push( pilot );
                        break;
                    case 'A2':
                        a2List.push( pilot );
                        break;
                    case 'A3':
                        a3List.push( pilot );
                        break;
                    case 'B1':
                        b1List.push( pilot );
                        break;
                    case 'B2':
                        b2List.push( pilot );
                        break;
                    case 'B3':
                        b3List.push( pilot );
                        break;
                    case 'C':
                        cList.push( pilot );
                        break;
                
                    default:
                        break;
                }
                
            });

            setSsRankPilots(ssList);
            setS1RankPilots(s1List);
            setS2RankPilots(s2List);
            setS3RankPilots(s3List);
            setA1RankPilots(a1List);
            setA2RankPilots(a2List);
            setA3RankPilots(a3List);
            setB1RankPilots(b1List);
            setB2RankPilots(b2List);
            setB3RankPilots(b3List);
            setCRankPilots(cList);
    }

    const filterPlayers = ( plat, country, sort, dir, includeLegacy ) => {
        if ( vctPlayers ) {
            let alphaPlayers = [];
            let newPlayers = [];

            if ( includeLegacy ) {
                alphaPlayers = vctPlayers.slice();
            } else {
                for (const player of vctPlayers) {
                    if ( player.active ) {
                        alphaPlayers.push( player ); 
                    }
                }
            }

            if ( plat === 'all' ) {
                newPlayers = alphaPlayers.slice()
            } else {
                for (const player of alphaPlayers) {
                    if ( player.platforms.indexOf( plat ) > -1 ) {
                        newPlayers.push( player ); 
                    }
                }
            }

            let secondFilterVct = [];

            if ( country === 'all' ) {
                secondFilterVct = newPlayers.slice();
            } else {
                for (const player of newPlayers) {
                    if ( player.country.code.indexOf( country ) > -1 ) {
                        secondFilterVct.push( player ); 
                    }
                }
            }

            if ( sort === 'Name' ) {

                secondFilterVct.sort(function (a, b) {
                    let nameA = a.name;
                    let nameb = b.name;

                    if ( dir === 'ascending' ) {
                        return (nameA < nameb) ? -1 : (nameA > nameb) ? 1 : 0;
                    } else {
                        return (nameA > nameb) ? -1 : (nameA < nameb) ? 1 : 0;
                    }
                });
            } 
            else if ( sort === 'Rank' ) {
                secondFilterVct.sort(function (a, b) {
                    if ( dir === 'ascending' ) {
                        return (a.rankAverage > b.rankAverage) ? -1 : (a.rankAverage < b.rankAverage) ? 1 : 0;
                    } else {
                        return (a.rankAverage < b.rankAverage) ? -1 : (a.rankAverage > b.rankAverage) ? 1 : 0;
                    }
                });
            }

            let lastVct = [];

            if ( showHiddenPlayers ) {
                lastVct = secondFilterVct.slice();
            } else {
                for (const player of secondFilterVct) {
                    if ( !player.hidden ) {
                        lastVct.push( player ); 
                    }
                }
            }

            lastVct.forEach((pilot, idx) => {
                pilot.posn = includeLegacy ? idx : idx+1;
            });

            setFilteredPlayers( lastVct );

            groupPlayersByRank( lastVct );
        }
    }

    const onChangeHandler = (e) => {
        console.log(e);
        const newArrayRef = [...filteredPlayers];
        let newArray = [];
        console.log( filteredPlayers );
        if ( e ) {
            newArray = newArrayRef.filter((d)=>{
                return d.name.toLowerCase().indexOf( e.toLowerCase() ) > -1 ||
                d.callsign.toLowerCase().indexOf( e.toLowerCase() ) > -1 ||
                d.unit.toLowerCase().indexOf( e.toLowerCase() ) > -1 ||
                d.mainPlane.toLowerCase().indexOf( e.toLowerCase() ) > -1 ||
                d.mainSpW.toLowerCase().indexOf( e.toLowerCase() ) > -1
            });
        }
        else {
            newArray = newArrayRef.filter((d)=>{return true});
        }
        
        console.log(newArray);
        groupPlayersByRank( newArray );
    }

    return (
        <div className="pilot-list-container tw-flex tw-flex-col tw-flex-grow tw-px-4 tw-pt-12 tw-pb-24 tw-h-screen">
            <div>{t('pilots.platformLbl')}:</div>
            <PlayersFilter selectFilter={ selectPlatformFilter } filter={ platformFilter } />
            
            <div>{t('pilots.countryLbl')}:</div>
            <div className="tw-flex tw-flex-row tw-justify-around tw-py-2 tw-items-center">
                <div onClick={ () => selectCountryFilter('all') } className={`player-list-item-name ${countryFilter === "all" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                    {t('common.all')}
                </div>
                <div className={`country-filter tw-flex tw-flex-row tw-overflow-visible tw-overflow-x-auto tw-scrollbar-thin ${scrollThumb} ${scrollTrack}`}>
                { vctCountries.map( country => <div className="tw-flex tw-flex-row tw-items-center tw-px-2" onClick={ () => selectCountryFilter(country.code) }>
                    <ReactCountryFlag countryCode={ country.code } style={{ fontSize: "2em" }} aria-label={ country.code }/>
                    <h3 className={`tw-pl-1 ${countryFilter === country.code ? "active-player-list-item" : "inactive-player-list-item"}`}>{country.cant}</h3>
                    </div> 
                )}
                </div>

            </div>
            
            <div className="form__group field">
                <input type="input" className="form__field" placeholder="Name" name="name" id='name' onChange={ (e) => onChangeHandler(e.target.value) }/>
                <label for="name" className="form__label">{t('pilots.filterLbl')}...</label>
            </div>
            
            <div className={`tw-overflow-visible tw-overflow-y-auto tw-scrollbar-thin ${scrollThumb} ${scrollTrack} tw-mt-2`}>
                <span className={`${ssRankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - SS -</span>
                { ssRankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}

                <span className={`${s1RankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - S1 -</span>
                { s1RankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}

                <span className={`${s2RankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - S2 -</span>
                { s2RankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}

                <span className={`${s3RankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - S3 -</span>
                { s3RankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}

                <span className={`${a1RankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - A1 -</span>
                { a1RankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}

                <span className={`${a2RankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - A2 -</span>
                { a2RankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}

                <span className={`${a3RankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - A3 -</span>
                { a3RankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}
    
                <span className={`${b1RankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - B1 -</span>
                { b1RankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}

                <span className={`${b2RankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - B2 -</span>
                { b2RankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}

                <span className={`${b3RankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - B3 -</span>
                { b3RankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}

                <span className={`${cRankPilots.length === 0 ? 'tw-hidden' : 'tw-flex'}`}>- {t('pilots.rankLbl')} - C -</span>
                { cRankPilots.map( (player, index) => (
                    <PlayerListItem
                        key={player.id} 
                        player={player} 
                        onSelect={ onSelectPilot } 
                        index={ player.posn }
                    />
                ))}
                
            </div>

            <div className="tw-flex tw-flex-row tw-justify-around tw-py-2">
                <span>{t('pilots.includeLegacyLbl')}:</span>
                <span onClick={ () => setLegacyPilots( true ) } className={`player-list-item-name ${countLegacyPilots ? "active-player-list-item" : "inactive-player-list-item"}`}>{t('common.yes')}</span>
                <span onClick={ () => setLegacyPilots( false ) } className={`player-list-item-name ${!countLegacyPilots ? "active-player-list-item" : "inactive-player-list-item"}`}>{t('common.no')}</span>
            </div>
        </div>
    )
}

export default PlayerList
