import React from "react";

import RulesLogo from "../../icons/rules.svg";
import Breadcumb from "../Breadcumb";

const RulesPage = () => {
  return (
    <div
      className={`tw-flex-row tw-h-screen tw-w-screen tw-justify-center tw-flex`}
    >
      <div className="tw-flex tw-flex-col 2xl:tw-w-3/5 lg:tw-w-9/12 tw-w-screen tw-p-4">
        <div className="tw-flex tw-flex-row tw-items-center">
          <Breadcumb secondLevel="Rules" iconSrc={RulesLogo}></Breadcumb>
          <span className="tw-flex tw-mx-auto"></span>
        </div>

        <div className="tw-overflow-visible tw-overflow-y-auto tw-scrollbar-thin">
          <h3>
            For players who want their profiles added or stats increased, here's
            the submission policy:
          </h3>

          <section className="tw-py-10">
            <h2>
              &gt; Method 1: Video / Youtube link Submission (S3 and above).{" "}
            </h2>

            <div className="tw-flex sm:tw-flex-row tw-flex-col">
              <div className="tw-flex tw-flex-col sm:tw-w-1/2 rule-box tw-p-4">
                <img
                  src={process.env.PUBLIC_URL + `/images/rules/r1.png`}
                  alt="rule"
                  width="128"
                  height="128"
                />
                <p className="rule-text">
                  1. Must be at the very least 2 sets of 3 matches each (No win
                  and leave, 6 total, the two sets can be played against
                  different opponents at different times)
                </p>
              </div>
              <div className="tw-flex tw-flex-col sm:tw-w-1/2 rule-box tw-p-4">
                <img
                  src={process.env.PUBLIC_URL + `/images/rules/r2.png`}
                  alt="rule"
                  width="128"
                  height="128"
                />
                <p className="rule-text">
                  2. Submitted matches cannot be of the same squad.
                </p>
              </div>
            </div>

            <div className="tw-flex sm:tw-flex-row tw-flex-col">
              <div className="tw-flex tw-flex-col sm:tw-w-1/2 rule-box tw-p-4">
                <img
                  src={process.env.PUBLIC_URL + `/images/rules/r3.png`}
                  alt="rule"
                  width="128"
                  height="128"
                />
                <p className="rule-text">
                  3. Matches should be recorded in their entirety.
                </p>
              </div>
              <div className="tw-flex tw-flex-col sm:tw-w-1/2 rule-box tw-p-4">
                <img
                  src={process.env.PUBLIC_URL + `/images/rules/r4.png`}
                  alt="rule"
                  width="128"
                  height="128"
                />
                <p className="rule-text">
                  4. Same rank and above, decision increases the higher the rank
                  of your opponent in relation to you (A1 vs S3 has more weight
                  than A1 vs A1).
                </p>
              </div>
            </div>

            <div className="tw-flex sm:tw-flex-row tw-flex-col">
              <div className="tw-flex tw-flex-col sm:tw-w-1/2 rule-box tw-p-4">
                <img
                  src={process.env.PUBLIC_URL + `/images/rules/r5.png`}
                  alt="rule"
                  width="128"
                  height="128"
                />
                <p className="rule-text">
                  5. Recorded Matches cannot be more than 4 weeks old.
                </p>
              </div>
              <div className="tw-flex tw-flex-col sm:tw-w-1/2 rule-box tw-p-4">
                <img
                  src={process.env.PUBLIC_URL + `/images/rules/r6.png`}
                  alt="rule"
                  width="128"
                  height="128"
                />
                <p className="rule-text">
                  6. Projectile vs Missile matchups are not allowed. Projectile
                  mirrors matches are allowed for only one set. (3 out of 6).
                </p>
              </div>
            </div>

            <div className="tw-flex sm:tw-flex-row tw-flex-col">
              <div className="tw-flex tw-flex-col sm:tw-w-1/2 rule-box tw-p-4">
                <img
                  src={process.env.PUBLIC_URL + `/images/rules/r7.png`}
                  alt="rule"
                  width="128"
                  height="128"
                />
                <p className="rule-text">
                  7. Missile only matchups matchups and special weapons matches
                  of the same weapon can be non-mirrored planes. Weapons that do
                  not match performance by a large margin (qaam vs hpaa) (msl vs
                  special weapon) are not allowed.
                </p>
              </div>
            </div>
          </section>

          <section className="tw-flex sm:tw-flex-row tw-flex-col tw-py-10">
		  	
			<div className="tw-flex tw-flex-col sm:tw-w-1/2 tw-p-4">
				<h2>&gt; Soft rules (2 or more to fill).</h2>
				<ol className="rule-text">
				<li>1. More than 3 games per set (that makes it 6 + (x))</li>
				<li>2. SpW off for at least one set of 3 matches.</li>
				<li>3. Different players for different sets.</li>
				</ol>
			</div>
		
			<div className="tw-flex tw-flex-col sm:tw-w-1/2 tw-p-4">
				<h2>&gt; Cherry Picking defense.</h2>
				<p className="rule-text">
				For those in the discord, and you find that the video submission
				of a particular player is cherry picking wins, please show your
				own version of those sets of matches to a judge.
				</p>
			</div>
          </section>

          <section className="tw-py-10">
            <h2>&gt; Admin Rules (as of 2/18/2022).</h2>
            <ol className="rule-text">
              <li>
                1. Same squad members cannot increase the stats of their own
                squad members, however, they can decrease or remove their own
                squad profiles
              </li>
              <li>
                2. With the exception of their own squad members, judges are
                allowed to alter and push up the stats of a lower tier up to A1
                without documentation, this is to allow less painful bureaucracy
                for those who have shown improvement on the lower tiers
              </li>
            </ol>
          </section>
        </div>
      </div>
    </div>
  );
};

export default RulesPage;
