
import useSound from "use-sound"
import NavigateSfx from "../../sfx/navigate.mp3";

const PlayerListItem = ({ player, onSelect, index }) => {

    const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );

    return (
        <div className="tw-flex tw-flex-row tw-py-1 tw-items-center">
            <div className={`${player.selected ? "active-caret" : "inactive-caret"}`}>

            </div>

            <div className="player-list-item-name tw-pl-4" onClick={() => onSelect( player )} onMouseEnter={ playNavigate }>
                <p className={`tw-text-2xl ${player.selected ? "active-player-list-item" : "inactive-player-list-item"}`}>
                    <div className="tw-flex tw-flex-row">
                        <span className={`${index === -1 ? "tw-hidden" : ""}`}>{ index }. </span>
                        &nbsp;
                        <span>{player.name}</span>
                        &nbsp;
                        <div className={`${player.active ? 'tw-hidden' : 'tw-flex'} tw-content-center`} style={{height:24}}>
                            <img src={process.env.PUBLIC_URL + "/images/icons/medal.png"} alt="U" width="24" height="24" />
                        </div>
                        <div className={`${player.isSquadLeader ? 'tw-flex' : 'tw-hidden'}`} style={{height:24}}>
                            <img src={process.env.PUBLIC_URL + "/images/icons/favourites.png"} alt="L" width="24" height="24" />
                        </div>
                    </div>
                </p>
            </div>
        </div>
    )
}

export default PlayerListItem
