
import { useState, useEffect } from "react"
import useSound from "use-sound"

import Breadcumb from "../Breadcumb"
import PlayerData from "./PlayerData"
import PlayerList from "./PlayerList"
import InformationButton from "./InformationButton"

import PilotLogo from "../../icons/pilot.svg";
import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";
import CancelSfx from "../../sfx/cancel.mp3";

import { rankingCoefficientCalculation } from './rankingCoefficientCalculation';

import { FaBars, FaWindowClose } from "react-icons/fa";

import { useTranslation, Trans } from 'react-i18next';

const Pilots = () => {
    const { t, i18n } = useTranslation();
    
    useEffect(() => {
        document.title = `Pilots - AC7 PSM MP Pilots DB`;
    });

    const [pilots, setPilots] = useState([]);
    const [selectedPilot, setSelectedPilot] = useState([]);
    const [showForMobile, setShowForMobile] = useState(false)
    const [pilotBreadcumb, setPilotBreadcumb] = useState('');
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const getPilots = async () => {
            const pilotsFromServer = await fetchPilots();
            setPilots(pilotsFromServer);
            
            if ( pilotsFromServer.length > 0 ) {
                //selectPilot( pilotsFromServer[0] );
            }

            setLoading(false);
        }

        getPilots();
    }, [])


    
    useEffect(() => {
        function handleResize() {
            if ( window.innerWidth > 1024 ) {
                setShowForMobile(false);
            }
        }
        window.addEventListener('resize', handleResize)
    });

    const fetchPilot = async (id) => {
        let url = process.env.NODE_ENV === 'development' ? "http://localhost:5000/" + id : process.env.PUBLIC_URL + "/database/" + id + ".json";
        const res = await fetch(url)
        const data = await res.json()

        return data;
    }

    const fetchPilots = async () => {
        let url = process.env.NODE_ENV === 'development' ? "http://localhost:5000/pilots" : process.env.PUBLIC_URL + "/database/pilotList.json";
        const res = await fetch(url)
        const data = await res.json()
        let pilots = data.pilots ? data.pilots : data;

        pilots.sort(function (a, b) {
            var textA = a.name.toUpperCase();
            var textB = b.name.toUpperCase();
            return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
        });

        pilots.forEach(player => {
            player.rankAverage = rankingCoefficientCalculation (
                player.statistics.mslAcc, 
                player.statistics.mslCurving, 
                player.statistics.mslLooping, 
                player.statistics.mgAcc, 
                player.statistics.mgRate, 
                player.statistics.boost, 
                player.statistics.consistency, 
                player.statistics.strategy, 
                player.statistics.drift, 
                player.statistics.bkf
            );
        });

        return pilots;
    }

    const [playSelect] = useSound(SelectSfx, { volume: 1 });
    const [playNavigate] = useSound(NavigateSfx, { volume: 1 });
    const [playCancel] = useSound(CancelSfx, { volume: 1 });

    const selectPilot = async (pilot) => {
        //const pilotFromServer = await fetchPilot(pilot.id);

        let mtPilot = pilot;
        mtPilot.selected = true;

        let mtPilots = pilots;
        for (const p of mtPilots) {
            p.id === pilot.id ? p.selected = true : p.selected = false;
        }

        setPilots( mtPilots );
        setSelectedPilot( pilot );
        setShowForMobile( false );
        setPilotBreadcumb( pilot.name );

        playSelect()
    }

    const displayPointsInformation = () => {
        let dg = document.getElementById("modal-information");
        dg.style.display = "block";
        playSelect()
    }

    const dimissInformationModal = () => {
        let dg = document.getElementById("modal-information");
        dg.style.display = "none";
        playCancel();
    }

    const toggleMenu = () => {
        let newVal = showForMobile;
        setShowForMobile(!newVal);
        console.log(showForMobile);
    }

    return (
        <>
        <div className={`tw-flex-row tw-h-screen tw-w-screen tw-justify-center tw-items-center ${loading ? 'tw-flex' : 'tw-hidden'}`}>
            <h2>{t('common.loadingLbl')}...</h2>
        </div>

        <div className={`tw-flex-row tw-h-screen tw-w-screen tw-justify-center ${loading ? 'tw-hidden' : 'tw-flex'}`}>


            <div id="modal-information" className="tw-absolute tw-flex tw-flex-col lg:tw-p-4 tw-overflow-auto">
                
                <div className="tw-flex sm:tw-flex-row tw-flex-col">

                    <div className="tw-flex tw-flex-col tw-justify-center tw-p-8 sm:tw-w-1/2">
                        <p className="tw-text-3xl lg:tw-text-4xl">Ranking and Status information:<br /><br /></p>

                        <p className="tw-text-base lg:tw-text-lg tw-text-justify">
                            <span className="tw-text-2xl">Strategy</span> - The ability of a pilot to use critical decision making, adaptive styles, disciplined maneuvering sequences, and energy fighting to maximize their efficiency in a dogfight
                            <br /><br />
                            <span className="tw-text-2xl">Boost</span> - The skill to control the second phase or psm called the boost (grip) phase  to gain angles in a dogfight
                            <br /><br />
                            <span className="tw-text-2xl">Drift</span> - The ability to use the first phase of psm (drifting) to create angles for the boost phase or use as a transition to other psm sequences
                            <br /><br />
                            <span className="tw-text-2xl">Backward Flying</span> - The skillset of inducing backwards flight with rechained psms to create angles for attack and defense in a psm fights
                            <br /><br />
                            <span className="tw-text-2xl">Consistency</span> - being able to use all your skills and strategies to achieve positive results against opponents throughout a session of dogfights. It is also the ability to force your flying style and strategy to work outside of ones comfort zone. This can be fighting across different platforms, different rules, and against unfamiliar flying styles
                        </p>
                    </div>

                    <div className="tw-flex tw-flex-col tw-justify-center tw-p-8 sm:tw-w-1/2">
                        <p className="tw-text-3xl lg:tw-text-4xl">Average Values:</p>

                        <p className="tw-text-base lg:tw-text-lg tw-text-justify tw-pt-10">
                            The ranking system works with an algorithm that weights the player skillset statistics.<br></br> 
                            In brief, the resulting average will be applied as follows:
                            <br /><br />
                            <span>SS Rank</span> - from 97.1 to 100.0 
                            <br /><br />
                            <span>S1 Rank</span> - from 92.1 to 97.0
                            <br />
                            <span>S2 Rank</span> - from 86.1 to 92.0
                            <br />
                            <span>S3 Rank</span> - from 80.1 to 86.0
                            <br /><br />
                            <span>A1 Rank</span> - from 74.1 to 80.0
                            <br />
                            <span>A2 Rank</span> - from 67.1 to 74.0
                            <br />
                            <span>A3 Rank</span> - from 60.1 to 67.0
                            <br /><br />
                            <span>B1 Rank</span> - from 54.1 to 60.0
                            <br />
                            <span>B2 Rank</span> - from 47.1 to 54.0
                            <br />
                            <span>B3 Rank</span> - from 40.1 to 47.0
                            <br />
                            <span>C Rank</span> - from 0.0 to 40.0
                        </p>
                    </div>

                </div>

                <div className="tw-m-6">

                    <span className="tw-text-lg" onClick={dimissInformationModal}>[OK]</span>
                </div>
                
            </div>
            

            <div className="tw-flex tw-flex-col 2xl:tw-w-3/5 lg:tw-w-9/12 tw-w-screen tw-p-4">
                <div className="tw-flex tw-flex-row tw-items-center">
                    <Breadcumb secondLevel={t('home.pilots')} thirdLevel={pilotBreadcumb} iconSrc={PilotLogo}></Breadcumb>
                    <span className="tw-flex tw-mx-auto"></span>
                    <span className="tw-text-xl lg:tw-hidden" onClick={toggleMenu}><FaBars /></span>
                </div>
                
                <div className={`${selectedPilot.rankAverage ? 'tw-hidden' : ''} tw-flex tw-flex-row tw-items-center`}>
                    <h2>{t('pilots.selectPilotLabel')}</h2>
                </div>

                <PlayerData player={selectedPilot}></PlayerData>

                <div className="tw-flex tw-my-auto">

                </div>
                <InformationButton onMouseEnterAction={playNavigate} onClickAction={displayPointsInformation} />
            </div>

            <div className={`2xl:tw-w-1/5 lg:tw-w-3/12 tw-w-screen ${showForMobile ? 'tw-flex tw-fixed' : 'lg:tw-flex tw-hidden'}`}>
                <div className={`tw-w-screen tw-p-4 tw-flex tw-justify-end ${showForMobile ? 'tw-absolute' : 'lg:tw-hidden'}`}>
                    <span className="tw-text-xl" onClick={toggleMenu}><FaWindowClose /></span>
                </div>
                <PlayerList vctPlayers={pilots} onSelectPilot={selectPilot}></PlayerList>
            </div>
        </div>
        </>
    )
}

export default Pilots
