
import './styles.css';

import { useState, useEffect } from "react"

import Breadcumb from '../Breadcumb';
import StatusLabel from "../players/StatusLabel"
import PilotLogo from "../../icons/pilot.svg";

import { rankingCoefficientCalculation, determineRankByAverage } from '../players/rankingCoefficientCalculation';

const AverageTest = () => {

    const [mslAcc, setMslAcc] = useState(0);
    const [mslCurving, setMslCurving] = useState(0);
    const [mslLooping, setMslLooping] = useState(0);
    const [mgAcc, setMgAcc] = useState(0);
    const [mgRate, setMgRate] = useState(0);
    const [boost, setBoost] = useState(0);
    const [consistency, setConsistency] = useState(0);
    const [strategy, setStrategy] = useState(0);
    const [drift, setDrift] = useState(0);
    const [bkf, setBkf] = useState(0);

    const [playerAvrg, setPlayerAvrg] = useState(0.0);
    const [playerRank, setPlayerRank] = useState("--");

    const updateAverage = (label, value) => {
        let bMslAcc=mslAcc, bMslCurv=mslCurving, bMslLoop=mslLooping, bMgAcc=mgAcc, bMgRate=mgRate, bBoost=boost, bCons=consistency, bStrat=strategy, bDrift=drift, bBkf=bkf;

        switch (label) {
            case 'bMslAcc':
                setMslAcc( value );
                bMslAcc=value;
                break;

            case 'bMslCurv':
                setMslCurving( value );
                bMslCurv=value;
                break;

            case 'bMslLoop':
                setMslLooping( value );
                bMslLoop=value;
                break;

            case 'bMgAcc':
                setMgAcc( value );
                bMgAcc=value;
                break;

            case 'bMgRate':
                setMgRate( value );
                bMgRate=value;
                break;
                
            case 'bBoost':
                setBoost( value );
                bBoost=value;
                break;

            case 'bCons':
                setConsistency( value );
                bCons=value;
                break;

            case 'bStrat':
                setStrategy( value );
                bStrat=value;
                break;

            case 'bDrift':
                setDrift( value );
                bDrift=value;
                break;

            case 'bBkf':
                setBkf( value );
                bBkf=value;
                break;

            default:
                break;
        }


        let average = rankingCoefficientCalculation (bMslAcc, bMslCurv, bMslLoop, bMgAcc, bMgRate, bBoost, bCons, bStrat, bDrift, bBkf );
        let rnk = determineRankByAverage( average );
        
        setPlayerAvrg( average.toFixed( 1 ) );
        setPlayerRank( rnk );
    }
    
    return (
        <div className="tw-flex tw-flex-col tw-overflow-auto lg:tw-overflow-visible tw-scrollbar-thin tw-scrollbar-thumb-blue-700 tw-scrollbar-track-blue-300">

            <div className="tw-flex tw-flex-row tw-items-center">
                <Breadcumb secondLevel="Average Tests" iconSrc={PilotLogo}></Breadcumb>
            </div>

            <div className='tw-flex tw-flex-col tw-w-48'>

                <StatusLabel text="[MSL Accuracy]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bMslAcc', e.target.value)} />

                <div className='tw-pt-4'></div>
                <StatusLabel text="[MSL Curving]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bMslCurv', e.target.value)} />

                <div className='tw-pt-4'></div>
                <StatusLabel text="[MSL Looping]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bMslLoop', e.target.value)} />

                <div className='tw-pt-8'></div>

                <StatusLabel text="[MG Accuracy]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bMgAcc', e.target.value)} />

                <div className='tw-pt-4'></div>
                <StatusLabel text="[MG Use Rate]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bMgRate', e.target.value)} />

                <div className='tw-pt-8'></div>

                <StatusLabel text="[Boost]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bBoost', e.target.value)} />

                <div className='tw-pt-4'></div>
                <StatusLabel text="[Drift]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bDrift', e.target.value)} />

                <div className='tw-pt-4'></div>
                <StatusLabel text="[Consistency]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bCons', e.target.value)} />

                <div className='tw-pt-4'></div>
                <StatusLabel text="[Backward Flying]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bBkf', e.target.value)} />

                <div className='tw-pt-4'></div>
                <StatusLabel text="[Strategy]" />
                <input className='testText' type="number" min="0" max ="100" onChange={e => updateAverage('bStrat', e.target.value)} />

                <div className='tw-pt-8'></div>
            </div>
            
    
            <div>
                <img src={`images/badges/${ playerRank }.png`} alt={playerRank} height="64" width="64"></img>
            </div>
            <div>
                <StatusLabel text={`[AVERAGE:${ playerAvrg }]`} />
                <h1 className="tw-text-4xl md:tw-text-5xl tw-pb-6">RANK { playerRank }</h1>
            </div>
            
        </div>
    )
}

export default AverageTest
