
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { useState, useEffect } from 'react';

import HomePage from "./components/home/HomePage";
import PilotsPage from "./components/players/PilotsPage";
import SettingsPage from './components/configuration/SettingsPage';
import AboutPage from './components/about/AboutPage';

import Music from './components/background-music/Music';
import useSound from 'use-sound';
import LoadedAppSfx from "./sfx/load_complete.mp3";
import SelectLoadSfx from "./sfx/select_load.mp3";

import Changelog from './components/changelog/Changelog';
import RulesPage from './components/rules/RulesPage';
import AverageTest from './components/average-test/averageTest';

import { selectFontTypeClass } from './components/configuration/SettingsPage';

import { useTranslation, Trans } from 'react-i18next';

function App() {
	const { t } = useTranslation();

	const [playLoadedApp] = useSound(LoadedAppSfx, {volume:1});
	const [playLoadingApp] = useSound(SelectLoadSfx, {volume:1});
	const [ playingMusic, setPlayingMusic ] = useState(false);

	const authenticate = () => {
		return new Promise(resolve => setTimeout(resolve, 1000)) // 2 seconds
	}

	useEffect(() => {
		if ( window.location.pathname !== '/' ) {
			window.location.replace('/');
		}
		
		authenticate().then(() => {
			const ele = document.getElementById('ipl-progress-indicator')
			if (ele) {
				playLoadingApp();
				// fade out
				ele.classList.add('available')
				setTimeout(() => {
					// remove from DOM
					ele.outerHTML = ''
				}, 2000)
			}
		})
	});

	const preloadSettings = () => {
		let font = JSON.parse(localStorage.getItem('font'));
		let theme = JSON.parse(localStorage.getItem('theme'));
		let body = document.getElementsByTagName('body')[0];

		loadTheme( theme, body );
		loadFont( font, body );
	}

	const loadTheme = ( theme, body ) => {
		console.log( theme );
		if ( !theme ) {
			document.body.classList.toggle('ac7-theme');
			document.body.classList.remove('acz-theme');
		} else {

			if ( theme === '7' ) {
				document.body.classList.toggle('ac7-theme');
				document.body.classList.remove('acz-theme');
				document.body.classList.remove('ac5-theme');
				
			} else if ( theme === 'Z' ) {
				document.body.classList.toggle('acz-theme');
				document.body.classList.remove('ac7-theme');
				document.body.classList.remove('ac5-theme');
	
			} else if ( theme === '5' ) {
				document.body.classList.toggle('ac5-theme');
				document.body.classList.remove('ac7-theme');
				document.body.classList.remove('acz-theme');
			}
		}
	}

	const loadFont = ( font, body ) => {
		if ( !font ) {
			body.style.fontFamily = "'AC7-Regular', sans-serif";
		} else {
			body.style.fontFamily = selectFontTypeClass( font );
		}
	}

	const onBegin = (evt) => {
		const ele = document.getElementById('modal-begin');
		preloadSettings();
		if (ele) {
			// fade out
			ele.classList.add('available')
			ele.outerHTML = ''

			var eMusic = new Event("keydown");
			eMusic.which = 119;
			eMusic.keyCode = 119;
			document.dispatchEvent(eMusic);

			/* View in fullscreen */
			if ( process.env.NODE_ENV !== 'development' ) {
				var elem = document.documentElement;
				if (elem.requestFullscreen) {
					elem.requestFullscreen();
				} else if (elem.webkitRequestFullscreen) { // Safari
					elem.webkitRequestFullscreen();
				} else if (elem.msRequestFullscreen) { // IE11
					elem.msRequestFullscreen();
				}
			}

			playLoadedApp();
			setPlayingMusic(true);
		}
	}

	return (
		<Router>
			<div className="App">
				<div id="modal-begin" className="tw-w-screen tw-h-screen tw-absolute tw-flex tw-flex-col tw-p-4">
					<div className="tw-flex tw-flex-col tw-mx-auto tw-my-auto tw-justify-center">
						<p className="tw-text-2xl tw-text-center" onClick={onBegin}>
							{t('home.initClick')}
						</p>
					</div>
				</div>

				<Routes>
					<Route path='/' element={<HomePage />} />
					<Route path='/pilots' element={<PilotsPage />} />
					<Route path='/rules' element={<RulesPage />} />
					<Route path='/settings' element={<SettingsPage />} />
					<Route path='/about' element={<AboutPage />} />
					<Route path='/changelog' element={<Changelog />} />
					<Route path='/average-test' element={<AverageTest />} />
				</Routes>
				
			</div>
			
			<Music musicState={playingMusic} />
		</Router>
	);
}
// https://youtu.be/w7ejDZ8SWv8?t=2982

export default App;
